import { Component, Host, h } from '@stencil/core';

@Component({
	tag: 'o-footer',
	styleUrl: 'o-footer.scss',
	shadow: true
})
export class OFooter {
	/** The content to display in the footer when no custom content is provided. */
	render() {
		return (
			<Host>
				<footer>
					<div class="px-6 bg-background-default border-t border-border-default">
						<div class="flex justify-between py-4 body-sm text-content-default my-2">
							<slot></slot>
						</div>
					</div>
				</footer>
			</Host>
		);
	}
}
